@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base{
  body{
    box-sizing: border-box;
    overflow: hidden;
    background-color: rgb(32, 32, 32);
    min-height: 100vh;
    min-width: 100vw;
    color: white;
  }

  a{
    color: inherit;
  }

  ::-webkit-scrollbar{
    width: 5px;
  }
  
  ::-webkit-scrollbar-thumb {
    background: #333333;
  }
  
  .margin-left-top{
    margin: 0 0 8px 10px;
  }
}

.file-link {
  display: flex;
  align-items: center; /* Вирівняти елементи по вертикалі */
}

.file-link svg {
  margin-right: 5px; /* Проміжок між іконкою і текстом */
}
